<template>
     <div class="insureResult" v-if="isShow">
    <div class="result-container ">
      <div class="result-wrapper">
        <div class="result success" v-if="result.code">
          <img src="../assets/images/insured_succeed.png">
          <div class="result-title result-title-success">投保成功！<span>已出单</span></div>
        </div>
        <div class="result fail" v-else>
          <img src="../assets/images/insured_fail.png">
          <div class="result-title">投保失败！</div>
        </div>

      </div>
      <div class="product-info">
        <p class="title-price">
          <span class="title">一日游发</span>
          <span class="price">400<i>元</i></span>
        </p>
        <div class="info " v-if="result.code">
          <ul>
            <li><i>被保险人姓名</i><span>发发发</span></li>
            <li><i>证件号码</i><span>发发发</span></li>
            <li><i>投保日期</i><span>发发发</span></li>
            <li><i>保险期间</i><span>发发发</span></li>
            <li><i>保单号</i><span>发发发</span></li>
          </ul>
        </div>
        <div class="info fail-reason " v-else>
          <p><span>原因：</span>这里是投保失败原因这里是投保失败原因这里是投保失败原因</p>
        </div>
      </div>
      <div class="btns">
        <div class="btn toDetail" @click="toDetail">查看订单详情</div>
        <div class="btn toIndex" @click="toIndex">返回首页</div>
      </div>
      <span class="c-b" v-text="result.data.name" v-if="result.code"></span>
      <span class="c-s" v-if='result.code' v-text="status( result.data.status)"></span>
      <div class="sn" v-if='result.code'>保单号: {{result.data.policy_num}}</div>
      <div class="sn" v-if="!result.code">{{result.message}}</div>

    </div>

  </div>
</template>

<script>
import { Indicator } from 'mint-ui'
export default {
  data () {
    return {
      isShow: false,
      result: {}
    }
  },
  mounted () {
    this.getInsuredResult()
    if (!this.isShow) {
      Indicator.open('加载中...')
    }
  },
  methods: {
    getInsuredResult () {
      this.$api.product.getInsuranceResult({
        orderSn: this.$route.params.orderSn
      })
        .then(res => {
          if (res.code === 9) {
            setTimeout(this.getInsuredResult, 10000)
            this.isShow = false
          } else {
            this.isShow = true
            this.result = JSON.parse(JSON.stringify(res))
            console.log('result', this.result)
          }
        })
    },
    status (status) {
      let res
      switch (status) {
        case 0:
          res = '待支付'
          break
        case 1:
          res = '已支付'
          break
        case 2:
          res = '已承保'
          break
        case 3:
          res = '已出单'
          break
        case 4:
          res = '保障中'
          break
        case 5:
          res = '已失效'
          break
        case 6:
          res = '订单失败'
          break
        case 7:
          res = '已取消'
          break
      }
      return res
    },
    toDetail (orderSn) {
      this.$router.push({
        path: '/myOrder/detail/' + this.$route.params.orderSn,
        query: {
          orderId: this.$route.params.orderSn
        }
      })
    },
    toIndex () {
      this.$router.push('/')
    }
  }

}
</script>

<style lang="scss" scoped>
  @import '@/assets/styles/mixin.scss';

  .insureResult {
    .result-wrapper {
      box-shadow: 0px 2px 4px 0 rgba(219, 219, 219, .35);
      .result {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: pxToRem(60px) 0 pxToRem(80px);
        img {
          display: block;
          width: pxToRem(92px);
          height: pxToRem(92px);
          padding-right: pxToRem(40px);
        }
        .result-title{
          font-size: pxToRem(36px);
          color: #FF4B4B;
          span{
            display: block;
            font-size: pxToRem(28px);
            color: #B6BBBB;
            padding-left:pxToRem(24px);
            margin-top:pxToRem(18px);
          }
        }
        .result-title-success{
          color: $color-active-text  ;
        }
      }

    }
    .product-info {
      background: #fff;
      padding: 0 pxToRem(60px);
      .title-price {
        display: flex;
        justify-content: space-between;
        height: pxToRem(96px);
        line-height: pxToRem(96px);
        font-size: pxToRem(36px);
        color: $color-text;
        border-bottom:$border-bottom-light-color;
        .price {
          i {
            font-size: pxToRem(24px);
            color: #D5D8D8;
          }
        }
      }
      .info{
        font-size:pxToRem(28px) ;
        color: #000000;
        line-height: pxToRem(44px) ;
        height:pxToRem(283px);
        display: flex;
        align-items: center;
        ul{
          width: 100%;
          padding-top: pxToRem(22px);
          li{
            font-size: pxToRem(24px) ;
            line-height:pxToRem(32px) ;
            display: flex;
            justify-content: space-between;
            margin-bottom: pxToRem(18px);
            i{
              color: #A5A8A8;
            }
            span{
              color: #1C1D1D;
            }
          }
        }
        span{
          color: #FF4B4B;
        }
      }
    }
    .btns {
      margin-top: pxToRem(120px);
      .toDetail {
        @include longBtn;
      }
      .toIndex {
        @include longLightBtn;
        margin-top: pxToRem(50px);
      }
    }
  }
</style>
